.main {
  background-color: black;
  border-top: none;
  box-shadow: 0 0 0 4px red, 0 0 0 8px orange, 0 0 0 12px red;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Press Start 2P', cursive;
  margin: 0 15px;
  width: 100%;
  height: 100%;
  max-height: 100%;
}

.main__container {
  height: 100%;
  overflow: hidden;
}

.main__title {
  font-size: 2.5rem;
  color: orange;
  -webkit-text-stroke: 1px red;
}

@media (max-width: 1000px) {
  .main__title {
    font-size: 1.75rem;
  }
}

@media (max-width: 660px) {
  .main__title {
    font-size: 1rem;
  }
}

@media (max-width: 390px) {
  .main__title {
    font-size: 0.75rem;
  }
}
